.content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start; 
    margin-top: 50px;
    height: calc(100vh - 77px);
    padding-top: 0; 
    /* margin-left: 100px; */
  }
  
  .detail-heading {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    color: #000000;
    margin-bottom: 30px;
  }
  
  .barcode {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .sub-headings {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 20px; 
    /* color: blue; */
    font-weight: 500px;
    font-size: 20px;
  }
  
  .sub-headings p {
    margin: 5px 0;
    font-weight: 500;
    font-size: 24px;
    color: #000000;
  }
  
  .detail-heading2 {
    font-size: 24px;
    color: #000000;
    font-weight: 500;
  }
  
  .inputbar {
    
    width: 480px;
    height: 40px;
    margin: 0 auto 20px; 
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #D9D9D9;
  }
  
  .buttons {
    display: flex;
    justify-content: space-between;
    gap: 450px;
    margin-top: 40px;
    background-color: #FFFFFF;
  }
  
  .buttons button {
    padding: 8px 24px;
    border-radius: 4px;
    border: 1px solid #ccc;
    cursor: pointer;
    background-color: #FFFFFF;
    width: 160px;
    height: 40px;
  }
  
  .buttons button:last-child {
    background-color: #86D56A;
    color: #3E3E3E;
    border: 1px;
    border-radius: 8px;
    font-weight: 400;
    font-size: 18px;
  }
  
  .buttons button:first-child {
    background-color: #FFFFFF;
    color: #000000;
    border: 1px solid #000000;
    border-radius: 8px;
    font-weight: 400;
    font-size: 18px;
  }
  
  @media screen and (max-width: 768px) {
    .content-container {
      margin-left: 0;
      padding: 10px;
    }
  
    .heading, .sub-headings p, .heading2 {
      font-size: 20px;
    }
  
    .inputbar {
      max-width: 100%;
    }
  
    .buttons {
      gap: 10px;
    }
  
    .buttons button {
      width: 140px;
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .heading, .sub-headings p, .heading2 {
      font-size: 18px;
    }
  
    .buttons button {
      width: 120px;
      font-size: 14px;
    }
  }
  
  @media screen and (max-width: 320px) {
    .buttons {
      flex-direction: column;
      align-items: center;
    }
  
    .buttons button {
      width: 100%;
      max-width: 200px;
    }
  }