.report-container {
    display: flex;
    margin-top: 5px;
    /* overflow: auto; */
    justify-content: space-between;
  }
  
  .report-details-t {
    /* grid-column: span 9; */
    background-color: #D9D9D9;
    border-radius: 8px;
    /* height: 105px; */
    padding: 25px;
    display: flex;
    /* margin-left: 10px; */
    justify-content: center;
    flex-direction: column;
    width: 80%;
  
  }
  
  .report-actions {
    /* grid-column: span 3; */
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: center;
    /* position: sticky; */
    top: 0;
    /* width: 10%; */
  
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
    font-size: 0.875rem;
    /* width: 400px; */
  }
  
  .font-medium {
    font-weight: 500;
    font-size: 16px;
    color: rgba(0, 0, 0, 1);
  }
  
  .font-semibold {
    font-weight: 600;
    font-size: 20px;
  }
  
  .action-button {
    /* height: 60px; */
    /* width: 150px; */
    padding: 20px;
    background: transparent;
    border: 3px solid #86D56A;
    font-size: 18px;
    color: #3E3E3E;
    font-weight: 400;
    border-radius: 8px;
    cursor: pointer;
    white-space: nowrap;
    position: relative;
    transition: transform 0.2s ease;
  }
  
  .action-button:hover {
    transform: translateY(-2px);
  }
  
 