.planning-overview-heading{
    
   margin-left: 10px;
  margin-top: 10px;
  margin-right: auto;
  font-size: 1.6vw;
  font-weight: 600;
  color: #000000;
}
.planning-overview-metrics-container{
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 10px;
   margin-left: 10px; 

  margin-top: 0px;
  
 
}
.planning-metrics-box1{
  display: flex;
  flex-direction: row;
  gap: 20px;
  font-size: 25px;
  font-weight: 500;
  background-color: #FA7474;
  border-radius: 8px;
  width: 100px;
  padding: 1vw;
  height: 30px;
  align-items: center;
 
}
.planning-metrics-box4{
  display: flex;
  flex-direction: row;
  gap: 20px;
  font-size: 25px;
  font-weight: 500;
  background-color: #dded7f;
  border-radius: 8px;
  width: 100px;
  padding: 1vw;
  height: 30px;
  align-items: center;
 
}
.planning-metrics-box2{
  display: flex;
  flex-direction: row;
  gap: 30px;
  font-size: 25px;
  font-weight: 500;
  background-color: #FFD6B0;
  border-radius: 8px;
  width: 120px;
  padding: 1vw;
  height: 30px;
  align-items: center;


}
.planning-metrics-box3{
  display: flex;
  flex-direction: row;
  gap: 40px;
  font-size: 25px;
  font-weight: 500;
  background-color: #E9EFC5;
  border-radius: 8px;
  width: 150px;
  padding: 1vw;
  height: 30px;
  align-items: center;

}

.planning-last-refresh{
  display: flex;
  flex-direction: row;
}
.planning-left-part{    
font-size: 25px;
}
.planning-right-part button{
  /* margin-top: 20px; */
  color: red;
  /* margin-left: -100px; */
  cursor: pointer;
}

.planning-footer-container {
  display: flex;
  align-items:center ;
  justify-content: space-around;
  gap: 20px;
  margin: 20px 200px;
  padding: 10px;
  margin-left: 2px;
}

.planning-footer-container button {
  padding: 8px 16px;
  background-color: rgb(204, 204, 204);
  border: 1px solid rgb(62, 62, 62);
  border-radius: 4px;
  cursor: pointer;
  color: rgb(153, 153, 153)   ;
  margin-left: 200px;
}

.planning-footer-container button:first-child {
  background-color: rgb(204, 204, 204);
  height: 30px;
  font-size: 15px;
}

.planning-overview-page-details {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: 200px;
}

.planning-overview-page-details h1 {
  font-size: 24px;
  font-weight: 500;
  color:#3E3E3E;
  margin: 0;
}

.planning-overview-page-details p {
  font-size: 24px;
  color: #3E3E3E;
  margin: 0;
}

.planning-page-number-input {
  width: 100px;
  height: 20px;

  border-radius: 4px;
  text-align: center;
  font-size: 18px;
}

.planning-footer-container button:last-child {
  background-color: #00A651;
  color: white;
  border: none;
  height: 30px;
  font-size: 15px;
  
}


@media (max-width: 1024px) {
  .planning-overview-heading {
      /* margin-left: 10px; */
      /* margin-top: 80px; */
      font-size: 4vw;
      text-align: center;
  }

  .planning-overview-metrics-container {
      flex-direction: row;
      gap: 10px;
      margin-left: 0;
      /* margin-top: 10px; */
      align-items: center;
  }

  .planning-metrics-box1,
  .planning-metrics-box2,
  .planning-metrics-box3 {
      width: 'fit-content';
      height: auto;
      padding: 10px;
      font-size: 4vw;
      text-align: start;
  }

  .planning-last-refresh {
      flex-direction: column;
      align-items: center;
      margin-top: 20px;
  }

  .planning-left-part {
      font-size: 3vw;
      text-align: center;
  }

  .planning-right-part {
      margin-top: 10px;
  }

  .planning-right-part button {
      margin: 0;
  }

  .planning-overview-table .table-container {
      margin-left: 0;
      width: 100%;
  }

  .planning-footer-container {
      flex-direction: column;
      gap: 10px;
      margin: 10px auto;
      padding: 10px;
      width: 90%;
  }

  .planning-footer-container button {
      width: 100%;
      height: 40px;
      font-size: 16px;
  }

  .planning-overview-page-details {
      flex-direction: row;
      align-items: center;
      gap: 5px;
  }

  .planning-overview-page-details h1,
  .planning-overview-page-details p {
      font-size: 18px;
  }

  .planning-page-number-input {
      width: 80%;
      height: 40px;
      font-size: 16px;
  }

  .planning-sidebar {
      display: none; /* Hide sidebar */
  }
}
