.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:  rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.popup-card {
    width: 80vh; 
    margin-top: 40px;
    position: relative;
    max-height: 500px;
    padding: 20px;
    border-radius: 10px;
    background: #FFFFFF;
    text-align: center;
    /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}
.popup-card::-webkit-scrollbar{
    display: none;
}


.table-container {
    width: 100%;
    max-height: 400px; 
    overflow: scroll;
    border-collapse: collapse;
}


.table-container thead tr {
    background-color: #D9D9D9;
    position: sticky;
    overflow-y: hidden;
}

.table-container th {
    padding: 15px;
    font-size: 12px;
    font-weight: bold;
    text-align: left;
    color: #3E3E3E;
    overflow-y: hidden;
    border-bottom: 2px solid #ddd;
}


.table-container tbody tr {
    background-color: #FFFFFF;
    
    height: 50px;
    overflow: scroll;
}



.table-container td {
    padding: 15px;
    font-size: 12px;
    color: #333;
    text-align: left;
    border-bottom: 1px solid #ddd;
    
}

.checkbox-cell {
    text-align: center;
}

.checkbox {
    transform: scale(1.3);
    cursor: pointer;
}


.question-cell {
    text-align: left;
    font-size: 12px;
    color: #333;
}


  .close-icon {
    position: absolute; 
    top: 30px;          
    right: 50px;       
    cursor: pointer;
    width: 24px;
    height: 24px;

  }
.popup-header h2{
    margin-top: 20px;
    align-self: center;
    margin-left: 20px;
}
.comment-section{
    
    margin-top: 20px;
}
.comment-section input{
    width: 100%;
    height: 30px;
    border: 1px solid #D9D9D9;
    border-radius:5px;
    font-size: 12px;
}

@media (min-width: 1302px) {
    .popup-card {
        width: 100vh; 
        max-height: 80vh;
    }
}

@media (min-width: 1039px) {
    .popup-card {
        width: 100vh; 
        max-height: 80vh
    }
}

@media (max-height: 768px) {
    .popup-card {
        max-height: 80vh;
        width: 100vh;  /* Allow more height on smaller viewports */
    }
}